.appWrapper {
    width: 100%;
    height: 100vh;
    background: #335272;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bounce {

    background-color: #dadada;
    border-radius: 50%;
    animation: blink 3s linear infinite normal;
    box-shadow: 0 0 20px 0 white;
}

@keyframes blink {
    0% {
        opacity: 0.1;
        width: 10vw;
        height: 10vw;
    }
    50% {
        opacity: 1;
        width: 15vw;
        height: 15vw;
    }
    100% {
        opacity: 0.1;
        width: 10vw;
        height: 10vw;
    }
}

::-webkit-scrollbar {
    width: 4px;
    height: auto;
    border: 2px solid #d5d5d5;
}

::-webkit-scrollbar-track {
    border-radius: 0;
    background: #eeeeee;
}

::-webkit-scrollbar-thumb {
    border-radius: 0;
    background: #b0b0b0;
}